.enterEmail {
  background-color: rgba(255, 255, 255, 0.1);
  padding: 20px;
  width: 300px;
  border: none;
  outline: none;
  border-radius: 10px;
  font-family: poppins;
  font-weight: 600;
  transition: all 0s ease-in-out;
  color: lightgray;
}
.enterEmail:focus {
  background-color: rgba(255, 255, 255, 0.2);
}
.enterEmail:hover {
  background-color: rgba(255, 255, 255, 0.15);
}
.passResetButton {
  padding: 10px;
  outline: none;
  background-color: rgba(255, 255, 255, 0.1);
  border: none;
  border-radius: 10px;
  color: white;
  transition: all 0.2s ease-in-out;
  cursor: pointer;
  font-family: poppins;
  font-weight: bold;
}
.passResetButton:hover {
  background-color: rgba(255, 255, 255, 0.15);
}
.passResetButton:disabled {
  padding: 10px;
  outline: none;
  background-color: rgba(255, 255, 255, 0.5);
  border: none;
  color: rgba(0, 0, 0, 0.8);
}

@keyframes rotate {
  from {
    opacity: 0;
    transform: translateY(100px);
  }
  to {
    opacity: 1;
    transform: translateY(0px);
  }
}
.rotate {
  animation: ease-in-out 0.6s rotate;
}

@keyframes class3 {
  0% {
    transform: translateX(0px);
  }
  50% {
    box-shadow: 0px 0px 0px rgba(255, 255, 255, 0.1);
  }
  100% {
    transform: translateX(0px);
  }
}
.class3 {
  animation: class3 2s ease-in-out infinite;
}
